h2{
    color: var(--color-primary);
    margin-bottom: 3rem;
}

#github-streak-stats{
    width: 700px;
    margin: auto;
}


#github-stats-card{
    width: 500px;
   
}

#github-top-langs{
    width: 400px;
   height: 214px;
   margin-left: 50px;
   margin-top: 10px;
}

.main{
  width: 550px;
  margin: auto;
}


.activity{
    width: 60%;
    margin: auto;
}



@media screen and (max-width:750px){

#github-streak-stats{
    width: 300px;
    margin: auto;
}

#github-stats-card{
    width: 300px;
   
}

#github-top-langs{
    width: 300px;
   height: 150px;
   margin-left: 8px;
}
.main{
    position: relative;
    left: 0px;
}
}


@media screen and (max-width:1024px){
    /* .main{
        flex-direction: column;
    } */
    #github-streak-stats{
        width: 400px;
        margin: auto;
       
    }
    .main{
        width: 70%;
        margin: auto;
    }
}