.portfolio_container{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 2.5rem;
}

.project-card{
background: var(--color-bg-variant);
padding: 1.2rem;
border-radius: 2rem;
border: 1px solid transparent;
transition: var(--transition);
}

.project-card:hover{
border-color: var(--color-primary-variant);
background: transparent;
}

.project-card-image>img{
    border-radius: 0.5rem;
    overflow: hidden;
    transition: transform .2s; /* Animation */
    width: 320px;
    height: 150px;
  
}

.portfolio_item-image>img:hover{
    
    transform: scale(1.5)
}

.portfolio_item-image>h3{
    margin: 1.2 0 2rem;
    color: var(--color-white);
}

.portfolio_item-image>p{
    
color: var(--color-white);
text-align: justify;

}

.button{
    display: flex;
    gap: 1rem;
    margin-top: 18px;
    margin-bottom: 1rem;
    
}
.tech>h4{

color:aqua;
}

.html{
    color: aliceblue;
    font-size: 25px;
    padding-left: 6px; 
}
    


.css{
    color: whitesmoke;
    font-size: 25px;
    padding-left: 6px; 
}

.javascript{
    color: aliceblue;
    font-size: 25px;
    padding-left: 6px; 
}

.react{
    color: aliceblue;
    font-size: 25px;
    padding-left: 6px; 
}

.chakra{
    color: aliceblue;
    font-size: 25px;
    padding-left: 6px; 
}

.project-github-link{
    width: max-content;
  display: inline-block;
  color: var(--color-primary);
  padding: 0.75rem 1.2rem;
  border-radius:  0.4rem;
  cursor: pointer;
  border: 1px solid var(--color-primary);
  transition: var(--transition);
  text-decoration: none;
}

.project-github-link:hover{
    background: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}

.project-deployed-link{
    background: var(--color-primary);
color: var(--color-bg);
}


/* ---------Media Queries (Medium Devices)---------- */

@media screen and (max-width:1024px){
    .portfolio_container{
        grid-template-columns: 1fr 1fr;
        gap: 1.2rem;
    }

  }
  
  
  /* ---------Media Queries (Small Devices)---------- */
  
  @media screen and (max-width:600px){
    .portfolio_container{
        grid-template-columns: 1fr ;
        gap: 1rem;
    }
  
  }



