#nav-menu{
 
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1000;
    background-color: #1f1f38;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}
/* #nav-menu>nav>a{
color: black;
} */

#nav-menu>nav>ul{
  list-style: none;
  float: right;
}

nav>ul>li{
display: inline-block;
padding-left: 10px;
padding-right: 30px;
}

#nav-menu>nav{
height: 70px;
width: 100%;
}


.nav-link{
  width: max-content;
  display: inline-block;
  color: white;
  padding: 0.60rem 0.8rem;
  border-radius:  0.4rem;
  cursor: pointer;
  border: 1px solid var(--color-primary);
  transition: var(--transition);
  text-decoration: none;
}

/* .home,.about,.skills,.projects,.contact,.resume{
 
color: var(--color-bg);
} */

.nav-link:hover{
  background: var(--color-white);
  color: var(--color-bg);
  border-color: transparent;
}

.checkbtn{
  position: relative;
 float: right;
 margin-right: 40px;
  font-size: 30px;
  color: white;
  line-height: 80px;
  cursor: pointer;
  display: none;
}

#check{
  display: none;
}

label.logo{
  color: #4db5ff;
  font-size: 35px;
  line-height: 80px;
  padding: 5 100px;
  font-weight: bold;
}

/* ---------Media Queries (Medium Devices)---------- */

@media screen and (max-width:1024px){
  /* nav ul li a{
    font-size: 16px;
  }
   .checkbtn{
    display: block;
   }
   ul{
    position: fixed;
    height: 100vh;
    top: 80px;
   } */
   label.logo{
    display: none;
   }
}
  
  
  /* ---------Media Queries (Small Devices)---------- */
  
  @media screen and (max-width:600px){
    .checkbtn{
      display: block;
    }
   ul{
    position: fixed;
    width: 100%;
    height: 80vh;
    background: #1f1f38;
    top: 30px;
    left: -200%;
    text-align: center;
    transition: all .5s;
   }
   nav ul li{
    display: block;
    margin: 20px 0;
   }
   #check:checked ~  ul{
    left: 0;
   }
   label.logo{
    display: none;
   }
}