header{
    height: 100vh;
    margin-top: 7rem;
    overflow: hidden;
}

.header_container{
    text-align: center;
    height: 100%;
    position: relative;
}

/* =========CTA========= */

.cta{
    margin-top: 2.5rem;
    display: flex;
    gap:1.2rem;
    justify-content: center;
}

/* =========Header Socials========= */

.header_socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header_socials::after{
    content: "";
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

/* =========Header image========= */

.me{
    background: var(--color-primary);
    width: 22rem;
    height: 13.5rem;
    position: absolute;
    left: 34.6%;
    margin-top: 4rem;
    border-radius:12rem 12rem 0 0;
    
}

.me>img{
    overflow-clip-margin: content-box;
    overflow: clip;
    border-radius: 0.4em;
   
}


@media screen and (max-width:750px){
    header{
        height: 100vh;
    }
    .header_socials,.scroll_down{
        display: none;
    }
    .me{
        background: var(--color-primary);
        
        height: 40rem;
        position: absolute;
        left: -5%;
        margin-top: 4rem;
        border-radius:12rem 12rem 0 0;
        
    }
  /* overflow: hidden;
  padding: 5rem 1.5rem 1.5rem 1.5rem; */
  
    }
  
  
    @media screen and (max-width:1024px){
        header{
            height: 68vh;
        }
      .me{
    background: var(--color-primary);
    
    height: 13.5rem;
    position: absolute;
    right: 10px;
    margin-top: 4rem;
    border-radius:12rem 12rem 0 0;
}
    }

/* =========Scroll Down========= */
.scroll_down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
    text-decoration: none;
}